<template>
  <section>
    <h2>Skills</h2>
    <div class="skill-container">
      <span v-for="skill in skillsSorted" :key="skill" class="skill">
        {{ skill }}
      </span>
    </div>
	</section>
</template>

<script>
  export default {
    props: ['skills'],
    computed: {
      // Sort skills alphabetically and case insensitive
      skillsSorted() {
        return this.skills.slice().sort((a, b) => a.toLowerCase().localeCompare(b.toLowerCase()))
      }
    }
  }
</script>

<style lang="scss">
  .skill-container {
    display: flex;
    flex-wrap: wrap;
  }

  .skill {
    flex-shrink: 0;

    border-radius: 4px;
    background-color: #ddd;
    margin: 0 8px 8px 0;
    padding: 1px 10px;

    &:hover {
      background-color: #eee;
    }
  }
</style>