<template>
  <header ref="header">
    <h1 class="name">{{ name }}<span class="stylish-dot">.</span></h1>
  </header>
</template>

<script>
  export default {
    props: ['name', 'scrollTop'],
    data() {
      return {}
    },
    watch: {
      scrollTop(value) {
        const alpha = Math.max(0, Math.min(value / 140, .8))
        this.$refs.header.style.backgroundColor = 'rgba(240,240,240,' + alpha + ')'
      }
    }
  }
</script>

<style lang="scss">
  @import "../vars";

  header {
    position: fixed;
    z-index: 1;
    width: 100%;
    padding: 0 $padding-ss-0;
    text-align: center;
  }

  .name {
    margin: 0;
    font-size: 2em;
    font-family: 'Alata', 'Avenir', Helvetica, Arial, sans-serif;
  }

  .stylish-dot {
    font-size: 2em;
    color: $brand-color;
  }

  @media (min-width: $screen-size-2) {
    header {
      padding: 0 $padding-ss-2;
    }
  }

  @media (min-width: $screen-size-3) {
    header {
      padding: 0 $padding-ss-3;
    }
  }

  @media (min-width: $screen-size-4) {
    header {
      padding: 0 $padding-ss-4;
    }
  }
</style>