<template>
  <div id="app">
    <Header :name="data.name" :scrollTop="scrollTop" />
    <Content :data="data" />
  </div>
</template>

<script>
  import Header from './components/Header.vue'
  import Content from './components/Content.vue'

  // CV data to dynamically load in
  import data from "@/cvdata.js"

  export default {
    name: 'app',
    components: {
      Header, Content
    },
    data() {
      return {
        data,
        scrollTop: 0
      }
    },
    methods: {
      handleScroll() {
        // Get scroll height of window
        this.scrollTop = window.pageYOffset || window.scrollTop
      }
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll)
    },
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll)
    }
  }
</script>

