<template>
	<section class="projects">
		<h2>Projects</h2>
    <!-- Horizontal scrolling list -->
    <div class="projects-scroller-container">
      <div class="projects-scroller">
        <div class="projects-container" :style="containerStyle">
          <!-- Project frame -->
          <a v-for="project in projects" :key="project.title" :class="{project, link: project.url}" :href="project.url" rel="noopener noreferrer" target="_blank">
            <span class="title">
              {{ project.title }}
              <img v-if="project.url" src="@/assets/new_tab.svg" alt="Opens in new tab" class="new-tab-icon" />
            </span>
            <div class="thumbnail" :style="getBackgroundStyle(project.thumbnail)" />
            <span class="description">{{ project.description }}</span>
          </a>
        </div>
      </div>
      <!-- Left fading gradiant -->
      <div class="left-gradient" />
      <!-- Right fading gradiant -->
      <div class="right-gradient" />
    </div>
	</section>
</template>

<script>
  /**
   * title, thumbnail, link, description
   */
	export default {
    props: ['projects'],
    data() {
      return {
        cardWidth: 216,
        cardMarginRight: 16,
        scrollerPadding: 32
      }
    },
    mounted() {
      const mediaQuery = window.matchMedia('(min-width: 700px) and (max-width: 999px)')
      const vm = this
      const updatePadding = (match) => { vm.scrollerPadding = match ? 80 : 32 }
      mediaQuery.addListener(updatePadding)
    },
    computed: {
      /*
      Total width = number of cards * width of card + 2 * padding - card margin right
      */
      containerStyle() {
        return 'width: ' + (this.projects.length * this.cardWidth + 2 * this.scrollerPadding - this.cardMarginRight) + 'px;'
      }
    },
    methods: {
      getBackgroundStyle(image) {
        return 'background-image: url("' + image + '");'
      }
    }
  }
</script>

<style lang="scss">
  @import "../vars";

  $scroller-padding: 32px;
  $scroller-padding-big: 80px;

  .projects {

    .projects-scroller-container {
      position: relative;
      height: 216px;

      .projects-scroller {
        position: absolute;
        left: -$scroller-padding;
        top: 0;
        /* '# { }' needed inside calc() */
        width: calc(100% + #{$scroller-padding + $scroller-padding});
        overflow-x: auto;
        padding-bottom: 20px; /* scrollbar */

        .projects-container {
          padding-left: $scroller-padding;

          a.project {
            display: inline-grid;
            margin-right: 16px;
            max-width: 200px;
            // Does not appear as link by default
            cursor: default;
            pointer-events: none;

            &.link {
              cursor: pointer;
              pointer-events: auto;

              &:hover > .thumbnail {
                transform: scale(1.05);
                box-shadow: 0px 4px 8px 0px #999;
              }
            }

            .title, .description {
              display: block;
              padding: 0 8px;
            }

            .title {
              font-size: 1.2em;
              font-family: 'Alata', 'Avenir', Helvetica, Arial, sans-serif;

              .new-tab-icon {
                width: 12px;
                height: 12px;
              }
            }

            .thumbnail {
              overflow: hidden;
              margin: 8px 0;
              border-radius: 12px;
              box-shadow: 0px 2px 8px 0px #999;

              width: 200px;
              height: 128px;
              background-size: cover;
              background-repeat: no-repeat;
              background-position: 50% 50%;
              transition: transform 0.1s ease-in-out;
            }

            .description {
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 3; /* number of lines to show */
              line-height: 18px; /* fallback */
              max-height: 54px;
            }
          }
        }
      }

      .left-gradient, .right-gradient {
        position: absolute;
        width: $scroller-padding;
        height: 216px;
        display: none;
      }

      .left-gradient {
        background: linear-gradient(90deg, #fff, #ffffff11);
      }

      .right-gradient {
        background: linear-gradient(-90deg, #fff, #ffffff11);
      }
    }
  }

  @media (min-width: $screen-size-2) {
    .projects {
      .projects-scroller-container {
        .projects-scroller {
          left: -$scroller-padding-big;
          /* '# { }' needed inside calc() */
          width: calc(100% + #{$scroller-padding-big + $scroller-padding-big});

          .projects-container {
            padding-left: $scroller-padding-big;
          }
        }
      }
    }
  }

  @media (min-width: $screen-size-3) {
    .projects {
      .projects-scroller-container {

        .projects-scroller {
          left: -$scroller-padding;
          /* '# { }' needed inside calc() */
          width: calc(100% + #{$scroller-padding + $scroller-padding});

          .projects-container {
            padding-left: $scroller-padding;
          }
        }

        .left-gradient, .right-gradient {
          display: block;
        }

        .left-gradient {
          left: -$scroller-padding;
        }

        .right-gradient {
          right: -$scroller-padding;
        }
      }
    }
  }
</style>